@import "../../assets/scss/setting.scss";
@import "../../assets/scss/mixin.scss";

.filter {
  @extend %flex-box;
  margin: $space $space 0;
  border-bottom:1px solid $border-color;
  li {
    position: relative;
    flex: 1;
    font-size: 0.28rem;
    line-height: 0.68rem;
    text-align: center;
    background-color: $white;
    &::after{
        position: absolute;
        right:.4rem;
        top:.3rem;
        content:' ';
        width:0;
        height:0;
        border-width:.1rem .1rem 0 ;
        border-style:solid;
        border-color: #333 transparent transparent;/*透明 透明  灰*/
    }
  }
  .active{
      color:$primary-color;
      &::after{
        border-color:$primary-color transparent transparent ;/*透明 透明  灰*/
    }
  }
}
