@import './setting';

@font-face {
  font-family: '#{$iconFontName}';
  src: url('#{$iconPath}#{$iconFontName}.eot'); /* IE9*/
  src: url('#{$iconPath}#{$iconFontName}.eot#iefix') format('embedded-opentype'), /* IE6-IE8 */
        url('#{$iconPath}#{$iconFontName}.woff') format('woff'), /* chrome, firefox */
        url('#{$iconPath}#{$iconFontName}.ttf') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
        url('#{$iconPath}#{$iconFontName}.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.icon-font {
  position: relative;
  font-family: $iconFontName;
  font-style:normal;
  color:$iconColor;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  &::before, &::after{
      font-weight: 100;
  }
}

