@import "../../../assets/scss/setting";

.swiper-box {
  position: relative;
  height: 3.6rem;
  background-color: rgba(0, 0, 0, 0.5);
  color: $white;
  & > div {
    height: 100%;
  }
  .item {
    width: 100%;
  }
  .swiper-lazy {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: none;
  }
  .swiper-lazy-loaded {
    display: block;
  }
  .shadow {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    height: 40%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6)) center top no-repeat;
    padding-left: 0.15rem;
  }
  h2 {
    font-size: 0.32rem;
  }
  .slogan {
    font-size: 0.24rem;
  }
}

:global(.swiper-pagination-bullet-active){
  background: $primary-color;
}
