@import "../../assets/scss/setting.scss";
@import "../../assets/scss/mixin.scss";

.item {
  padding: 0.15rem;
  background-color: $white;
  &:last-child {
    margin-bottom: $space;
  }
  a {
    @extend %flex-box;
  }
  img {
    flex: 0 0 1.8rem;
    width: 1.8rem;
    height: 1.35rem;
    margin-right: 0.15rem;
    object-fit: cover;
  }
  .item--right {
    @extend %flex-box;
    flex: auto;
    flex-direction: column;
  }
  figcaption {
    font-size: 0.26rem;
  }
  .intro {
    font-size: 0.24rem;
    color: $gray;
    margin-top: 0.15rem;
    line-height: 1.3em;
    flex: auto;
  }
  .sale-msg {
    @extend %flex-box;
    @extend %flex-row-center;
    font-size: 0;
    span {
      font-size: 0.3rem;
      color: $primary-color;
    }
    .tag {
      flex: auto;
      @extend %flex-box;
      @extend %flex-row-center;
    }
    .lxsj {
      display: inline-block;
      font-size: 0.16rem;
      margin-left: 0.15rem;
      border: 1px solid $orange;
      color: $orange;
      padding: 0.03rem 0.05rem;
    }
    small {
      font-size: 0.2rem;
      color: $gray;
    }
  }
}
