@import "../../assets/scss/setting";
@import "../../assets/scss/mixin";
@import "../../assets/scss/font.scss";

.menu{
  background-color: $white;
  font-size:.28rem;
  margin: $space;
  @extend %clearfix;
  li{
    float:left;
    text-align: center;
    display:block;
    width:25%;
    padding:.2rem 0;
  }
  .icon-font{
      display:block;
      margin:0 auto;
      font-size:.6rem;
      width:1rem;
      height:1rem;
      line-height:1rem;
      border-radius: 100%;
      margin-bottom:.1rem;
  }
  .icon-sc{
    background-color: $green;
  }
  .icon-fruits{
    background-color: $yellow;
  }
  .icon-meat{
    background-color: $pink;
  }
  .icon-gh{
    background-color: $brown;
  }
}