
//设置网站主色调
$primary-color: #2dae98;

//设置网站背景色
$site-bg-color: #ecfff3;

//设置模块间隔
$space: .2rem;

//border颜色
$border-color: #dedede;

//白色
$white: #fff;

//黑色
$black: #333;

//灰色
$gray: #666;

//绿色
$green: #87cb69;

