$yellow:#ffd83b;

//橙色
$orange: #ffa200;

//粉色
$pink:  #f46c62;

//棕色
$brown: #cbae69;

// 网站字体
$font-family: "Microsoft YaHei","Arial";

// 自定义字体路径
$iconPath: "../../assets/fonts/" !default;
$iconFontName: "iconfont" !default;
$iconColor: #fff !default;