@import "../../assets/scss/setting";
@import "../../assets/scss/font";
@import "../../assets/scss/mixin";

.header {
  @extend %flex-box;
  @extend %flex-row-center;
  background-color: $primary-color;
  height: 0.8rem;
  font-weight: 300;
  .icon-back {
    font-size: 0.52rem;
    padding-left: 0.05rem;
  }
  .title {
    color: $white;
    font-size: 0.4rem;
    flex: auto;
    text-align: center;
  }
  .icon-menu {
    font-size: 0.42rem;
    padding-right: 0.2rem;
  }
  .header--layer {
    position: absolute;
    right: 0.1rem;
    top: 0;
    z-index: 10;
    padding: 0;
    margin: 0;
    font-size: 0.3rem;
    background-color: rgba(255,255,255,.8);
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
    dt {
      line-height: 2em;
      padding: 0 0.3rem;
      text-align: center;
      border-bottom: 1px solid $border-color;
    }
  }
}
