@import "../../assets/scss/setting";
@import "../../assets/scss/font";
@import "../../assets/scss/mixin";
.header {
  @extend %flex-box;
  @extend %flex-row-center;
  height: 1rem;
  background-color: $primary-color;
  //定位图标
  .icon-location {
    max-width: 1.6rem;
    font-size: 0.3rem;
    padding: 0 0.1rem;
    @extend %ellipsis;
    &:before {
      font-size: 0.4rem;
      margin-right: 0.05rem;
      vertical-align: middle;
    }
  }
  //搜索框
  .icon-search {
    position: relative;
    flex: auto;
    height: 0.5rem;
    line-height: 0.5rem;
    background-color: #6cc7b8;
    border-radius: 0.05rem;
    box-sizing: border-box;
    font-size: 0.3rem;
    &:before {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      height: 100%;
      width: 0.5rem;
      text-align: center;
    }
    input[type="search"] {
      display: block;
      height: 100%;
      width: 100%;
      box-sizing: border-box;
      background-color: transparent;
      border: none;
      color: $white;
      line-height: 0.5rem;
      padding: 0 0.1rem 0 0.5rem;
    }
  }
  //用户icon
  .icon-user {
    font-size: 0.45rem;
    padding: 0 0.1rem;
    text-align: center;
  }
}
