$iconPath: "../../../assets/fonts/";

@import "../../../assets/scss/setting.scss";
@import "../../../assets/scss/reset.scss";
@import "../../../assets/scss/mixin.scss";
@import "../../../assets/scss/font.scss";

.goods-info {
  font-size: 0.24rem;
  background-color: $white;
  .price-box {
    @extend %flex-box;
    border-bottom: 1px solid $border-color;
    .goods-price {
      font-size: 0.36rem;
      color: $primary-color;
      padding: 0 0.1rem 0 0.15rem;
      line-height: 0.75rem;
    }
    .market-price {
      color: $gray;
      flex: auto;
      @extend %flex-box;
      @extend %flex-row-end;
      del {
        line-height: 2.5em;
      }
    }

    .icon-star-hollow ,.icon-star-solid{
      color: $primary-color;
      font-size: 0.4rem;
      line-height: 0.75rem;
      margin-right:.15rem;
    }
  }

  .goods-summary {
    padding: 0 0.15rem;
    line-height: 0.6rem;
  }
  .business-tag {
    padding: 0 0.15rem 0.1rem;
    .icon-credit {
      color: $orange;
      &::before {
        margin-right: 0.1rem;
        font-size: 0.32rem;
      }
    }
  }
}
