@import "../../../assets/scss/setting.scss";
@import "../../../assets/scss/reset.scss";
@import "../../../assets/scss/mixin.scss";

.comment {
  margin-bottom: 0.1rem;
  padding: .15rem;
  font-size: 0.3rem;
  .info {
    @extend %flex-box;
  }
  .avatar {
    border-radius: 50%;
    width: 1rem;
    height: 1rem;
    margin-right: 0.1rem;
  }
  .name-wraper {
    flex: auto;
  }
  .name {
    font-size: 0.28rem;
    padding: 0.1rem 0 0.3rem;
  }
  time {
    font-size: 0.22rem;
    color: $gray;
    padding-left: 0.1rem;
  }
  .message {
    font-size: 0.26rem;
    line-height: 2.5em;
  }
  .photo-wraper {
    height: 2rem;
    & > div {
      height: 100%;
    }
  }
  .photo{
      width:2rem;
      height:2rem;
      object-fit:cover;
  }
}


:global(.controls__1g_fd){
    top: 0.1rem;
    right: 0.1rem;
    background: rgba(255,255,255,0.6) !important;
}

:global(.controls__1g_fd .close__2iWfl){
    width:.5rem;
    height:.5rem;
    margin:.1rem !important;
}

:global(.controls__1g_fd .close__2iWfl > svg){
    width:100%;
    height:100%;
}
