//flex布局
%flex-box{
  display: -webkit-flex; /* Safari */
  display: flex;
}

// 基于左侧对齐
%flex-col-start{
  justify-content:flex-start;
}

//水平居中
%flex-col-center{
  justify-content:center;
}

// 基于右侧进行对齐
%flex-col-end{
  justify-content:flex-end;
}

//顶部对齐
%flex-row-start{
  align-items:flex-start;
}

//垂直居中
%flex-row-center{
  align-items:center;
}

//底部对齐
%flex-row-end{
  align-items:flex-end;
}


//设置字体
@function calculateRem($size) {
  $remSize: $size / 16px;
  @return $remSize * 1rem;
}

@mixin font-size($size) {
  font-size: $size;
  font-size: calculateRem($size);
}

//清除浮动
%clearfix {
  *zoom: 1;
  &:before, &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

//超出显示省略号
%ellipsis{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}